* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.list{
  list-style: none;
}

.country-list, .additional-info {
  display: flex;
  flex-direction: column;
  row-gap: 15px;
  padding-top: 10px;
  padding-left: 0;
}

.temp-list__item, .flag-and-name, .info-item {
  display:flex;
  align-items: center;
}

.flag {
  display: inline-block;
  margin-right: 15px;
}

.temp-list__name{
  margin: 0;
  font-size: 20px;
  line-height: 1;
}


.country-name {
margin: 0;
font-size: 35px;
}

.info-item, .info-name, .info-value {
  margin: 0;
  font-size: 22px;
  line-height: 1.2;
}

.info-value{
  margin-left: 15px;
  font-weight: 400;